html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

#root {
  height: 100vh;
  background-color: #4f4e4e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.containerApp {
  width: 75%;
  height: 75%;
  background-color: #ffff;
  overflow-y: hidden;
  border-radius: 2rem;
  -webkit-box-shadow: -4px 4px 51px 6px rgba(33, 33, 34, 1);
  -moz-box-shadow: -4px 4px 51px 6px rgba(33, 33, 34, 1);
  box-shadow: -4px 4px 51px 6px rgba(33, 33, 34, 1);
}

main {
  display: flex;
  border-top: 0.2rem solid #efeeee;
  font-family: "Ubuntu", sans-serif;
  width: 100%;
  height: calc(100% - 6.6rem);
}

@media (max-width: 600px) {
  main {
    display: flex;
    flex-direction: column-reverse;
    height: auto;
  }

  .containerApp {
    width: 100%;
    height: 100%;
    background-color: #ffff;
    border-radius: 0;
  }
}
